*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
}

.bg-light {
  background-color: #24bef9!important;
}
div#mainheader {
    padding-left: 0%;
    padding-right: 0%;
}
a.navbar-brand.active {
  padding-left: 4%;
}

.navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .show>.nav-link {
  color: var(--bs-white);
}
navbar-light .navbar-nav .nav-link {
  color: var(--bs-white);
}




.navbar{
   background-color: #fff !important;
}
.navbar-brand{
  font-size: 1.9rem !important;
  color: white !important;
}
.navbar a{
  font-size:  1.3rem;
  text-transform: capitalize;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid white;
}
.navbar:hover{
  color : orange ;
}

@media (max-width: 991px){
  .navbar-brand{
    font-size: 1.5rem !important;
  }
  .menu_active{
    font-weight: bold;
    border-bottom: none;
  }
}

/**********************************************************/
#header{
  width: 100%;
  height: 80vh;
  margin-top: 0px;
  background-color: #6610f2;
  padding-top: 21%;
  padding-bottom: 20%;
}
#header h1{
  margin: 0 0 10px 0;
  font-size:  48px;
  font-weight: 700;
  color: black;
}
#header .brand-name {
  color: #3fa9f1;
}
#header h2{
  color: #000000;
  font-size: 24px;
}
#header .btn-get-started{
  font-weight: 800;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover{
  background: #3498db;
  color: #fff;
}
.header-img {
  text-align: right;
}
img#home-img {
  width: 90%;
}
.col-lg-6.order-1.order-lg-2.header-img {
  margin-top: -2%;
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
/**contact us ***********/
.form-group {
  margin-bottom: 2%;
}
.container.contact-div {
  margin-top: -1%;
}
h1.text-center {
  font-weight: 900;
}

/***** Resposive Code ********/
@media (max-width: 991px){
  #header{
    height: 80vh;
    text-align: center;
  }
  #header .header-img{
    width: 60%;
  }
}
@media (max-width: 768px){
  #header{
   margin-top: 0px;
  }
  #header h1{
    font-size: 35px;
  }
  #header h2{
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img{
    width: 100%;
    margin-left: 37%;
  }
}


.card {
  margin: 5% 0% 5% 0%;
  color: black;
  /* height: 370px; */
}
img.card-img-top {
  height: 212px;
}